//import { questions } from './questions'
import { quiz } from './quizApp'
import { questions } from './translations'

let headerObject = {
  create: function (mobile, header, exNumber) {
    $('html, body').stop().animate({
       scrollTop: $('.card').offset().top
     }, 700);
    jQuery('.Appcontainer').empty();
    jQuery(".AppHeader").empty();
    jQuery('#buttons').empty();
    if (!header) {
      header = questions[quiz.settings.currentQuestion]['header']
    }



    let firstRow = `<h1 class="display-4">${quiz.settings.currentQuestion}/10 ${header}</h1>`;
    if (mobile) {
      firstRow= ` <h3>${quiz.settings.currentQuestion}/10 ${header}</h3>`
    }

    let template = `${firstRow}
    <progress class="progress" value="${quiz.settings.currentQuestion}0" max="100" aria-describedby="digitaitotesti">
        <div class="progress">
          <span class="progress-bar" style="width: ${quiz.settings.currentQuestion}0%;"></span>
        </div>
      </progress>`

    jQuery(".AppHeader").html(template)
    jQuery(document.createElement('p'))
      .addClass('lead')
      .attr('id', 'question')
      .html(questions[quiz.settings.currentQuestion]['question'])
      .insertAfter('progress');


  }
};

export { headerObject }
