let addItem = function (obj) {

  let listTemplate = `
  <label class="btn">
    <input type="checkbox" id="tsek" name='${obj[0]}'><i class="fa fa-square-o fa-2x"></i><i class="fa fa-check-square-o fa-2x"></i><span> ${obj[1]}</span>
  </label>
  `
  jQuery("#lista").append(listTemplate)
}



jQuery.each(translations.checkList, function( key, value ) {

  if (value) { addItem(value) };
});




jQuery('.lista').on('click', function () {

  let checked = jQuery('.btn.active').length
  if (checked === 1 ) {
  jQuery(document.createElement('a'))
    .attr('id', 'tarkista')
    .attr('title', translations.emailApp.IfRight)
    .html(' <button class="btn btn-danger">'+ translations.emailApp.IfRight + '</button>')
    .appendTo('#buttons')
    .click(function() { checkApp.process() });
  } else {
    jQuery('#tarkista').remove();
  }

  if (checked >= 2 ) {

      jQuery('input#tsek').each(function () {
        $(this).prop('checked', false)
      });
      jQuery('label.btn').each(function () {
        $(this).removeClass('active')
      });
      checked = 0
  }


})






