import { questions } from './translations'
import { quiz } from './quizApp'
import { translations } from './translations'


let footerObject = {
  create: function (exercise, points, rightNum) {
    jQuery('#buttons').css('margin-top', '20px')
    			jQuery("#tarkista").remove();
          jQuery('.Appcontainer').css('pointer-events', 'auto');
          let kuva = "";

    // if (quiz.settings.currentQuestion === 8)
    // {
    //   //kuva ='<img class="card-img-bottom" src="http://yle.fi/plus/other/2016_digitaitotesti/vastaus_blockbuster_teksteilla.jpg" alt="Card image cap" style="margin-bottom: 20px"> '
    // };

    let answer = `
    <div class="row">
      <div class="col-sm-12">
    <div class="card card-inverse card-block" style="background-color: #eee; border-color: #eee;">
      <h3 class="card-title" style="color: #5e5e5e"><i class="fa fa-times fa-2x" style="color: red"></i> ${translations.general.pickedWrong}</h3>
      <p class="lead" style="color: #5e5e5e">${questions[quiz.settings.currentQuestion].moreInfo}</p>
      <div id="buttonCont"></div>
    </div>

  </div>
    </div>

    `

    if (points >= 0) {
      answer = `
      <div class="row">
        <div class="col-sm-12">
      <div class="card card-inverse card-block" style="background-color: #eee; border-color: #eee;">
        <h3 class="card-title" style="color: #5e5e5e">${points}/6 ${translations.general.pickedRight}!</h3>
        <p class="lead" style="color: #5e5e5e">${questions[quiz.settings.currentQuestion].moreInfo}</p>
        <div id="buttonCont"></div>
      </div>
    </div>
      </div>

      `
    }

      if (rightNum >= 0 || rightNum === translations.imageApp.all || rightNum === translations.imageApp.pickedText) {
        answer = `
        <div class="row">
          <div class="col-sm-12">
        <div class="card card-inverse card-block" style="background-color: #eee; border-color: #eee;">
          <h3 class="card-title" style="color: #5e5e5e">${rightNum} ${translations.general.pickedRight}!</h3>
          <p class="lead" style="color: #5e5e5e">${questions[quiz.settings.currentQuestion].moreInfo}</p>
          ${kuva}
          <div id="buttonCont"></div>
        </div>
      </div>
        </div>

        `
      } else if (rightNum === translations.imageApp.wrongAnswer) {
        answer = `
        <div class="row">
          <div class="col-sm-12">
        <div class="card card-inverse card-block" style="background-color: #eee; border-color: #eee;">
          <h3 class="card-title" style="color: #5e5e5e">${translations.general.pickedWrong}</h3>
          <p class="lead style="color: #5e5e5e">${questions[quiz.settings.currentQuestion].moreInfo}</p>
        ${kuva}
          <div id="buttonCont"></div>
        </div>

      </div>
        </div>
        `
      }


        footerObject.collectPoints(exercise, points);


    jQuery('#buttons').append(answer);
    $('#nextQuestion').remove();
    jQuery(document.createElement('a'))
      .attr('id', 'nextQuestion')
      .attr('title', 'Seuraava kysymys')
      .html(`<button class="btn btn-danger">${translations.general.nextQuestion}</button>`)
      .appendTo('#buttonCont')

      //END quiz
      if (quiz.settings.quizLength === quiz.settings.currentQuestion) {

        jQuery('#nextQuestion').html(`<button class="btn btn-danger">${translations.general.resultsButton}</button>`)
      }

      jQuery('#nextQuestion').off('click').on('click', function () {
        quiz.settings.quizLength === quiz.settings.currentQuestion ? quiz.resultPage() : quiz.router(quiz.settings.currentQuestion + 1);
      });

  },
  collectPoints: function (exercise, sum) {
      quiz.exPoints["yhteensa"] += sum
      switch(exercise) {
        case 1:
            quiz.exPoints["symbolit"] = sum
            break;
        case 2:
           quiz.exPoints["nappainkomennot"] = sum
            break;
        case 3:
           quiz.exPoints["sahkoposti"] = sum
           break;
        case 4:
           quiz.exPoints["muotoilunPoisto"] = sum
            break;
        case 5:
          quiz.exPoints["tiedostokoot"] = sum
                break;
        case 6:
           quiz.exPoints["muistitila"] = sum
            break;
        case 7:
           quiz.exPoints["dataUlkomailla"] = sum
            break;
        case 8:
           quiz.exPoints["sovellukset"] = sum
            break;
        case 9:
          quiz.exPoints["verkonJakaminen"] = sum
            break;
        case 10:
          quiz.exPoints["haunTehostaminen"] = sum
          footerObject.toDatabase();
            break;
        default:
            return
      }
    },
  toDatabase: function ( ) {
      console.log("te" + quiz.exPoints.yhteensa)
      if (quiz.exPoints.yhteensa > 60) {
        quiz.exPoints.yhteensa = 60
      }
      var data = quiz.exPoints;         // get the values submitted in the form
      var url = "https://script.google.com/macros/s/AKfycbxTQZbQBetyPiXpDqNGbzljVjIS3l6ObffTYl7PIymS-vFCFobh/exec";  //
      var xhr = new XMLHttpRequest();
      xhr.open('POST', url);
      // xhr.withCredentials = true;
      xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
      xhr.onreadystatechange = function() {
          return;
      };
      // url encode form data for sending as post data
      var encoded = Object.keys(data).map(function(k) {
          return encodeURIComponent(k) + '=' + encodeURIComponent(data[k])
      }).join('&')
      xhr.send(encoded);
  },


}

export { footerObject }
