import jQuery from 'jquery';
import { quiz } from './quizApp'
import { headerObject } from './headerobject'
import { questions } from './questions'
import { footerObject } from './footerObject'
import { translations } from './translations'

let emailPoints = 0

let emailApp = {
	init: function () {
		$('html, body').stop().animate({
		   scrollTop: $('.AppHeader').offset().top
		 }, 700);
		headerObject.create(mobile, null, null);
	    let template = `
			<h4>Uusi viesti</h4>
			<form>

	<div class="form-group row" id="receiver">
		<label for="inputEmail3" class="col-sm-2 col-form-label">To:</label>
		<div class="col-sm-10">
			<input type="text" class="form-control" id="receiverInput" placeholder="${translations.emailApp.address}">
		</div>
	</div>
	<div class="form-group row" id="cc">
		<label for="inputPassword3" class="col-sm-2 col-form-label">Cc:</label>
		<div class="col-sm-10">
			<input type="text" class="form-control" id="kopioInput" placeholder="${translations.emailApp.address}">
		</div>
	</div>
	<div class="form-group row" id="bcc">
		<label for="inputPassword3" class="col-sm-2 col-form-label">Bcc:</label>
		<div class="col-sm-10">
			<input type="text" class="form-control" id="bccInput" placeholder="${translations.emailApp.address}">
		</div>
	</div>
	<div class="form-group row" id="subject">
		<label for="inputPassword3" class="col-sm-2 col-form-label">Subject:</label>
		<div class="col-sm-10">
			<input type="text" class="form-control" id="subjectInput" placeholder="kirjoita aihe" value="${translations.emailApp.subject}">
		</div>
	</div>
	<div class="form-group row">
		<label for="inputPassword3" class="col-sm-2 col-form-label">Message:</label>
		<div class="col-sm-10">
		<textarea class="form-control" id="exampleTextarea" rows="5" placeholder="${translations.emailApp.leaveEmpty}"></textarea>
		</div>
	</div>
</form>
<button style="margin-bottom: 10px" type="submit" class="btn btn-primary" id="tarkista">${translations.emailApp.IfRight}</button>

	`


	jQuery('#quiz-start').off('click').click(function() { quiz.router(4) });

	  jQuery(".Appcontainer").html(template)

		$.widget( "custom.catcomplete", $.ui.autocomplete, {
		 _create: function() {
			 this._super();
			 this.widget().menu( "option", "items", "> :not(.ui-autocomplete-category)" );
		 },
		 _renderMenu: function( ul, items ) {
			 var that = this,
				 currentCategory = "";
			 $.each( items, function( index, item ) {
				 var li;
				 if ( item.category != currentCategory ) {
					 ul.append( "<li class='ui-autocomplete-category'>" + item.category + "</li>" );
					 currentCategory = item.category;
				 }
				 li = that._renderItemData( ul, item );
				 if ( item.category ) {
					 li.attr( "aria-label", item.category + " : " + item.label );
				 }
			 });
		 }
	 });

		var data = [
		{ label: "heikki@foretag.fi", category: "" },
		{ label: "liisa@foretag.fi", category: "" },
		{ label: "riitta@foretag.fi", category: "" },
		{ label: "pekka@foretag.fi", category: "" },
		{ label: "pekka@foretag.fi, riitta@foretag.fi", category: "" },
		];

		$( "input" ).catcomplete({
      delay: 0,
      source: data
    });

		jQuery("#tarkista").click(function (event) {

			event.preventDefault ? event.preventDefault() : (event.returnValue = false);

			let receiver = $('#receiverInput').val();
			let cc = $('#kopioInput').val();
			let bcc = $('#bccInput').val();
			let subject = $('#subjectInput').val();



			let wrongAnswer = function (selector, cont, name) {
				let wrongTemplate = `
				<div class="form-group row has-danger">
				<label class="col-sm-2 col-form-label">${name}</label>
				<div class="col-sm-10">
					<input type="text" class="form-control form-control-danger" id="inputDanger1" value="${selector}">
				</div>
				</div>
				`

				jQuery('#' + cont).replaceWith(wrongTemplate)
			}

			let rightAnswer = function (selector, cont, name) {
				let rightTemplate=`<div class="form-group row has-success">
				<label class="col-sm-2 col-form-label">${name}</label>
				<div class="col-sm-10">
					<input type="text" class="form-control form-control-success" value="${selector}">
				</div>
				</div>`
				emailPoints += 2
				jQuery('#' + cont).replaceWith(rightTemplate)
			}

			receiver.indexOf("ekka@foretag.fi") >= 0 && receiver.indexOf("iitta@foretag.fi") >= 0
			? rightAnswer(receiver, "receiver", "To:")
			: wrongAnswer(receiver, "receiver", "To:");

			cc.indexOf("liisa@foretag.fi") >= 0
			? rightAnswer(cc, "cc", "Cc:")
			: wrongAnswer(cc, "cc", "Cc:");

			bcc.indexOf("heikki@foretag.fi") >= 0
			? rightAnswer(bcc, "bcc", "Bcc:")
			: wrongAnswer(bcc, "bcc", "Bcc:");


		footerObject.create(3, emailPoints);

		});

	}
}


 export { emailApp }
